
import { Password } from '@mui/icons-material';
import React, { useState, useEffect } from 'react'
import { NavLink, useNavigate } from 'react-router-dom'

const Login = () => {

    const homepage = useNavigate();
    const [val, setVal] = useState([]);
    // const[user,setuser]=useState(null); 
    const [mail, setMail] = useState({
        email: "",
        password: "",
    })


    const getmail = (e) => {

        const { value, name } = e.target;
        console.log(e.target.value);


        setMail(() => {
            return {
                ...mail, [name]: value
            }
        })
    }

    const Signin = (e) => {
        e.preventDefault()

        const getuserdata = localStorage.getItem("user")


        console.log(getuserdata, 'data');
        const { email, password } = mail

        if (email === "") {

            alert("Please enter your mail")
        } else if (!email.includes("@")) {
            alert("add  @ full mail")

        }
        else if (password === "") {


            alert("Please enter your password")
        } else if (password.length < 5) {

            alert("password length greater five")
        } else {

            if (getuserdata && getuserdata.length) {
                const userdata = JSON.parse(getuserdata);
                const userlogin = userdata.filter((item, vl) => {
                    return item.email === email && item.password === password
                });
                if (userlogin.length === 0) {

                    alert("Please enter valid details")
                } else {

                    // localStorage.setItem("user", JSON.stringify(getuserdata))
                    localStorage.setItem("token", true)
                    homepage("/home")
                    alert('user login succeusfully')
                }
            }
        }
    }
    return (
        <>
            <section class="vh-100" >
                <div class="container h-100">
                    <div class="row d-flex justify-content-center align-items-center h-100">
                        <div class="col-lg-12 col-xl-11">
                            <div class="card text-black"  >
                                <div class="card-body p-md-5">
                                    <div class="row justify-content-center">
                                        <div class="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">

                                            <p class="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4">Sign In</p>

                                            <form class="mx-1 mx-md-4">



                                                <div class="d-flex flex-row align-items-center mb-4">
                                                    <i class="fas fa-envelope fa-lg me-3 fa-fw"></i>
                                                    <div class="form-outline flex-fill mb-0">
                                                        <input onChange={getmail} type="email" id="form3Example3c"
                                                            name='email' class="form-control" />

                                                        <label class="form-label" for="form3Example3c">Your Email</label>
                                                    </div>
                                                </div>

                                                <div class="d-flex flex-row align-items-center mb-4">
                                                    <i class="fas fa-lock fa-lg me-3 fa-fw"></i>
                                                    <div class="form-outline flex-fill mb-0">
                                                        <input onChange={getmail} type="password" id="form3Example4c" name='password' class="form-control" />

                                                        <label class="form-label" for="form3Example4c">Password</label>
                                                    </div>
                                                </div>






                                                <div class="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                                    <button onClick={Signin} type="button" class="btn btn-primary btn-lg">Sign in</button>
                                                </div>



                                                <div className='mb-2'>

                                                    <h5> New user? <NavLink to="/signup">signUp</NavLink>   </h5>
                                                </div>

                                            </form>


                                        </div>
                                        <div class="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2">

                                            <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-registration/draw1.webp"
                                                class="img-fluid" alt="Sample image" />

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Login